/* ## Typographical Elements
--------------------------------------------- */
body {
    background-color: #ffffff;
    color: #333;
    color: #000;
    font-family: 'Avenir-Book', sans-serif;
    font-size: 18px;
    font-weight: 300;
    line-height: 1.4;
    margin: 0;
}

.button,
.gallery img,
a,
button,
input:focus,
input[type="button"],
input[type="reset"],
input[type="submit"],
textarea:focus {
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -ms-transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
}

::-moz-selection {
    background-color: #333;
    color: #fff;
}

::selection {
    background-color: #333;
    color: #fff;
}

a {
    color: #C95433;
    text-decoration: none;
}

a:hover {
    color: #aa3d1e;
}

p {
    margin: 0 0 24px;
    padding: 0;
    font-family: 'Avenir-Book';
}

ol,
ul {
    margin: 0;
    padding: 0;
}

li {
    list-style-type: none;
}

b,
strong {
    font-weight: 700;
}

.entry-content strong {
    font-family: 'Avenir-Medium';
}

blockquote,
cite,
em,
i {
    font-style: italic;
}

blockquote {
    margin: 40px 0 40px 20px;
}

blockquote::before {
    content: "\201C";
    display: block;
    font-size: 30px;
    height: 0;
    left: -20px;
    position: relative;
    top: -10px;
}

blockquote::after {
    content: "\201C";
    display: inline-block;
    font-size: 30px;
    height: 0;
    left: -10px;
    position: relative;
    top: -30px;
    float: right;
}

.hidden {
    display: none;
}
