/* ## Search Form
--------------------------------------------- */
.search-form1 {
    overflow: hidden;
    position: relative;
    right: 0;
    top: 9px;
    height: 38px;
}

.search-form1 .hidden {
    display: none;
}

input[type="searchform"] {
    width: 90px;
    height: 20px;
    padding: 12px 14px;
    font-size: 15px;
    color: #ffffff;
    -moz-transition: all 2s ease;
    -webkit-transition: all 2s ease;
    float: right;
    background-color: #000000;
    border: none;
}

.search-label {
    display: block;
    background: url("images/search-icon.png") no-repeat;
