/* jssor slider thumbnail navigator skin 11 css */
/* .jssort11 .p            (normal) .jssort11 .p:hover      (normal mouseover) .jssort11 .pav          (active) .jssort11 .pav:hover    (active mouseover) .jssort11 .pdn          (mousedown) */
#slider1_container {
    max-width: 800px !important;
}

.jssort11 {
    position: absolute;
    width: 310px;
    height: 330px;
    margin-top: 0;
    font-family: Arial, Helvetica, sans-serif;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.jssort11 .p {
    position: absolute;
    width: 260px;
    height: 103px;
    background: #000000;
    /*border: 1px solid #B7C9D1;*/
}

.jssort11 .tp {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
    padding: 10px;
    cursor: pointer;
}

.jssort11 .i,
.jssort11 .pav:hover .i {
    position: absolute;
    top: 3px;
    left: 3px;
    width: 60px;
    height: 30px;
    border: white 1px dashed;
}

* html .jssort11 .i {
    width: 62px;
    height: 32px;
}

.jssort11 .pav .i {
    border: white 1px solid;
}

.jssort11 .pav:hover .t,
.jssort11 .t {
    position: absolute;
    top: 3px;
    left: 68px;
    width: 129px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    color: #fc9835;
    font-size: 13px;
    font-weight: 700;
}

.jssort11 .p:hover .t,
.jssort11 .pav .t {
    color: #fff;
}

.jssort11 .pav:hover .slider-title,
.jssort11 .slider-title {
    position: absolute;
    top: 24px;
    left: 11px;
    width: 222px;
    height: 50px;
    line-height: 22px;
    color: #fff;
    font-size: 16px;
    font-family: 'Avenir-medium';
    overflow: hidden;
    /* text-transform: uppercase; */
}

.jssort11 .p:hover .slider-title,
.jssort11 .pav .slider-title {
    color: #ffffff;
}

.jssort11 .slider-title,
.jssort11 .t {
    transition: color 2s;
    -moz-transition: color 2s;
    -webkit-transition: color 2s;
    -o-transition: color 2s;
}

.jssort11 .p:hover .slider-title,
.jssort11 .p:hover .t,
.jssort11 .pav:hover .slider-title,
.jssort11 .pav:hover .t {
    transition: none;
    -moz-transition: none;
    -webkit-transition: none;
    -o-transition: none;
}

.jssort11 .p:hover,
.jssort11 .pav:hover {
    background: #333;
}

.jssort11 .p.pdn,
.jssort11 .pav {
    background: #738086;
}

.jssort11 .slider-title a:hover {
    color: #fff;
}
