/**
 * Popups
 */
.big-popup {
  &--black {
    background: #141213 url(images/popup-bg.png) bottom right;
    background-repeat: no-repeat;
    background-size: contain;
    border: 0;
    padding: 3px;
    color: #fff;
    max-width: 783px;
    width: 80vw;
    max-height: 80vh;
  }

  &__border {
    border: 1px solid #fff;
    padding: 20px;
    max-height: 80vh;
    box-sizing: border-box;
  }

  &__header {
    text-align: right;

    &__close {
      position: absolute;
      top: 10px;
      right: 10px;
      border-radius: 50%;
      font-size: 12px !important;
      border: 2px solid #fff;
      width: 14px;
      height: 14px;
      text-align: center;
      line-height: 14px !important;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 1;

      &::before {
        padding: 0;
        margin: 0;
        display: block;
        width: 15px;
        line-height: 14px;
        font-size: 14px;
      }

      &:hover {
        opacity: 0.6;
      }
    }
  }

  &__content {
    padding: 20px 130px;
    max-height: 57vh;
    overflow: auto;

    p {
      max-width: none;
    }

    &__title {
      font-family: 'callunabold';
      color: #fff;
    }

    &__button {
      background: #b4401e;
      border: 1px solid #b4401e;
      border: none;
      display: inline-block;
      color: #fff;
      overflow: hidden;

      &:hover {
        color: #fff;

        .big-popup__content__button__text {
          background: #b4401e;
        }
      }

      &__text {
        display: inline-block;
        vertical-align: middle;
        background: #C95433;
        border: none;
        padding: 7px 17px 6px 16px;
        transition: all 0.4s ease;
        font-size: 14px;
        font-family: 'Avenir-Book';
      }

      &__right {
        display: inline-block !important;
        vertical-align: middle;
        padding: 0 10px 0 4px;
        position: relative;
      }
    }
  }
}

.popup-subscribe {
  .big-popup__border {
    padding: 0;
  }

  &__header {
    text-align: left;
    background: #000;
    padding: 20px 50px;

    &__title,
    &__subtitle {
      color: #fff;
    }

    &__title {
      font-size: 28px;
      font-family: 'callunabold';
      margin: 0;
    }

    &__subtitle {
      font-size: 14px;
    }

    &__left,
    &__logo {
      display: inline-block;
      vertical-align: top;
    }

    &__left {
      width: calc(100% - 140px);
    }

    &__logo {
      float: right;
    }

  }

  &__content {
    padding: 20px 50px;

    .big-popup__content__button {
      margin-top: 30px;
    }
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    @extend .form-list;

    li {
      font-size: 14px;
    }
  }
}
