    &__expire {
      display: inline-block;
      vertical-align: bottom;
    }
    &__logo {
      width: 135px;
    }
    &__expire {
      width: calc(100% - 135px);
      text-align: right;
      font-size: 10px;
      color: white;
    }
  }

  &__titles {
    background: #f3f5f5;
    padding: 38px 20px;
  }
  &__title {
    font-size: 32px;
    margin: 0;
  }
  &__subtitle {
    font-size: 20px;
    padding: 0;
    margin: 0;
  }
  &__content {
    background: #f3f5f5;
    padding: 0 20px;
    box-sizing: border-box;
  }
  &__image {
    width: 250px;
    float: left;
    box-sizing: border-box;
  }
  &__text__content {
    width: 100%;
    box-sizing: border-box;
    padding-left: 265px;
    border-bottom: 1px solid #ccc;
    ul {
      li {
        position: relative;
        padding-left: 20px;
        font-size: 16px;
        margin-bottom: 5px;
        &::before {
          content: '\f00c';
          display: inline-block;
          font: normal normal normal 14px/1 FontAwesome;
          font-size: inherit;
          text-rendering: auto;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          background: black;
          color: white;
          font-size: 10px;
          width: 15px;
          height: 15px;
          line-height: 15px;
          text-align: center;
          border-radius: 50%;
          vertical-align: top;
          margin-top: 2px;
          position: absolute;
          left: 0;
        }
